@import url('https://fonts.googleapis.com/css2?family=Orbitron&family=Playfair+Display:ital@0;1&display=swap');

.gallery {

    figure{
      width: 100%;
      height: 100%;
      position: relative; 
      margin-top: -100px;
      background-image: url(../../../public/img/gallery06.jpg);
      background-size: cover;
      background-position: center bottom;
      background-repeat: no-repeat
      ;
      filter: saturate(50%);
      overflow: hidden;

      .galleryMian{
        position: relative;
        margin: 0 auto;
        width: 1180px;
        padding:50px;
        height:100%;
        box-shadow: 5px 5px 10px rgba(0,0,0,.7);
        margin-top: 150px;
        margin-bottom: 200px;

        video{
          width: 100%;
          height: 100%;
          object-fit: cover;
          filter: saturate(30%);
        }
  
        
      .explanation02{
        position: absolute;
        left: -250px;
        top: 55%;
        width: 200px;
        height: 200px;
  
        .exP2{
            font:normal 10px 'Orbitron';
            color:rgba(255,255,255,.8);
            letter-spacing: 1.2px;
            color:#fff;
            width: 200px;
            height: 100%;
            display: block;
            position: absolute;
            transform: rotate(-90deg);
  
        &::before{
            content: '';
            display: block;
            width: 2px;
            height: 80%;
            background: #fff;
            position: absolute;
            bottom:45%;
            right:90%;
            transform: rotate(90deg);
            }
  
          &::after{
              content: '';
              display: block;
              font-size: 20px;
              color:#fff;
              position: absolute;
              bottom:78%;
              right:125%;
              transform: rotate(-270deg);
              }
          }
      }
  
        .galleryContent{
          width: 45%;
          position: absolute;
          top: 50px;
          padding:50px 25px;
  
          h1{
            font:bold 75px/1 'Orbitron';
            color:rgba(255,255,255,.9);
            text-shadow: 5px 5px 10px rgba(255,255,255,.2);
            letter-spacing: 5px;
            margin-bottom: 50px;
            position: relative;
  
            &::before{
              content: '';
              display: block;
              width: 90%;
              height: 2.5px;
              background: rgba(255,255,255,.7);
              position: absolute;
              bottom: -15px;
              right: 30%;
            }
          }
      
          p{
            font:normal 10px/15px 'Orbitron';
            color:rgba(255,255,255,.3);
            letter-spacing: 1px;
          }
    
            
          strong{
            font:bold 10px/1 'Orbitron';
            color:rgba(255,255,255,.5);
            letter-spacing: 1.2px;
            margin-top: 30px;
            display: block;
          }
    
        }
  
      }

    h3{
      font:bold 100px/1 'Orbitron';
      color:rgba(255,255,255,.09);
      letter-spacing: 5px;
      position: relative;
      top:-75px;
      left: 5%;
    }

    .innerWrap {
      transform: translateY(200px);
      opacity: 0;
      transition: 1s;
      position: relative;
      width: 100%;
      bottom: 50px;
      height: 100%;

      >.inner {
        transition: none;
        width: 1180px;
        margin: 0px auto;
        position: relative;
        border:1px solid rgba(0,0,0, .01);
        height: 100%;
    

        .searchBox{
          position: absolute;
          top: 25px;
          right: 10px;

          input[type=text]{
            width: 167px;
            height: 35px;
            font-family: 'Orbitron';
            font-size: 10px;
            padding-left:5px;
            letter-spacing: 1.2px;
          }

          button{
            width: 50px;
            height: 35px;
            font-family: 'Orbitron';
            color:#fff;
            font-size: 10px;
            background: rgba(0,0,0,.5);
            border:none;
            cursor: pointer;
            letter-spacing: 1.2px;
          }
        }

        .loadingAni{
          position: relative;
          top: 250px;
      
          img{
            position: absolute;
            perspective: 800;
            opacity: 0;
      
            &:nth-child(1){
              width:300px;
              height: 200px;
              transform: rotateY(-180deg);
              position: absolute;
              left: 250px;
              animation: galleryAni01 10s;
            }
            
            &:nth-child(2){
              width:10px;
              height: 10px;
              animation: galleryAni02 9s 1s;
            }
      
            &:nth-child(3){
              width:15px;
              height:15px;
              animation: galleryAni03 7s 3s;
              animation-delay: 3s;
            }
          }
        }

        >section {
          transform: translateY(200px);
          opacity: 0;
          transition: 1s;
          margin-top: 75px;
          margin-bottom:50px;
    
          &.on {
            transform: translateY(0px);
            opacity: 1;
          }
    
          &::after {
            content: '';
            display: block;
            clear: both;
          }
    
          article {
            width: 20%;
            height: 250px;
            float: left;
            padding: 10px;
    
            >.inner {
              width: 100%;
              height: 100%;
              background: #fff;
              box-shadow: 5px 5px 10px rgba(0,0,0, .1);
              padding: 10px;
    
              .pic {
                width: 100%;
                height: 80%;
                margin-bottom: 10px;
                cursor: pointer;
    
                img {
                  width: 100%; 
                  height: 100%;
                }            
              }
    
              >b {
                width: 100%;
                height: 20%;
                display: block;
                font:normal 10px/1 'Orbitron';
                color:#555;
                letter-spacing: 2.5px;
                margin-bottom: 20px;
                text-overflow: ellipsis;   
                overflow: hidden;
                -webkit-box-orient: vertical;
                white-space:nowrap;
              }
            }
          }
        }
      } 
    } 
  
      &.on {
        .innerWrap {
          transform: translateY(0px); opacity: 1;
        }
      }
    }
  }
  
  .popup {
    width: 100%;
    height: 100%;
    background: rgba(0,0,0,.9);
    position: fixed;
    top: 0px;
    left: 0px;
    padding: 5vw;
    z-index: 6;
  
    img {
      width: 100%;
      height: 100%;
      object-fit: contain;
    }
  
    span {
      font: bold 14px/1 'arial';
      color: #fff;
      position: absolute;
      top: 4vh;
      right: 5vw; cursor: pointer;
    }
  }
  

  @keyframes galleryAni01{
    0%{left:250px; opacity: 0;}
    10%{opacity: 0.5;}
    90%{ opacity: 0.5;}
    100%{ left: 500px; opacity: 0;}
  }

  @keyframes galleryAni02{
    0%{left: 485px; top:65px; opacity: 0;}
    90%{opacity: 0.5;}
    100%{left: 700px; top:-10px; opacity: 0;}
  }

  @keyframes galleryAni03{
    0%{left: 590px; top:70px; opacity: 0;}
    90%{opacity: 0.5;}
    100%{left:700px; top:20px;opacity: 0;}
  }

  @keyframes galleryAni04{
    0%{left:20px; opacity: 0;}
    10%{opacity: 0.5;}
    90%{ opacity: 0.5;}
    100%{ left: 270px; opacity: 0;}
  }

  @keyframes galleryAni05{
    0%{left: 255px; top:65px; opacity: 0;}
    90%{opacity: 0.5;}
    100%{left: 470px; top:-10px; opacity: 0;}
  }

  @keyframes galleryAni06{
    0%{left: 360px; top:70px; opacity: 0;}
    90%{opacity: 0.5;}
    100%{left:470px; top:20px;opacity: 0;}
  }


 
  @media screen and(max-width:1559px) {

    .gallery {

        figure{

          .galleryMian{
            width: 90%;
      
          .explanation02{display: none;}
      
            .galleryContent{
              padding:30px 50px;

              p{
                overflow: hidden;
                text-overflow: ellipsis;   
                display: -webkit-box;
                -webkit-line-clamp: 9; 
                -webkit-box-orient: vertical;
              }
            }
          }

          h3{
            font:bold 80px/1 'Orbitron';
            top:-50px;
            left: 2.5%;
          }
    
          .innerWrap {

            >.inner {
              width: 100%;

              .searchBox{
                top: 25px;
                right: 0px;
      
                input[type=text]{width: 160px;}
              }

              >section {
          
                article {
          
                  >.inner {
          
                    >b {letter-spacing: 1.8px;}
                  }
                }
              }
            } 
          } 
        }
      }
}

@media screen and(max-width:$tablet) { 
  .gallery {

    figure{
      filter: saturate(30%);

      .galleryMian{
        width: 95%;
  
        .galleryContent{

          h1{font-size: 55px;}

          p{-webkit-line-clamp: 6;}
        }
      }

    
      h3{font:bold 70px/1 'Orbitron';}

      .innerWrap {

        >.inner {

          .searchBox{
            top: 25px;
            right: 10px;
  
            input[type=text]{height: 30px;}

            button{height: 30px;}
          }

          >section {
      
            article {
              width: 25%;

              >.inner {
      
                >b {letter-spacing: 1.4px;}
              }
            }
          }
        } 
      } 
    }
  }
}

@media screen and (max-width: 899px){
  .gallery {

    figure{
      filter: saturate(25%);

      .galleryMian{
        padding:30px;
        margin-top: 200px;

        video{height: 500px;}
  
        .galleryContent{
          padding:30px;

          h1{font-size: 50px;}

          p{-webkit-line-clamp: 8;}
        }
      }

    
      h3{
        font:bold 55px/60px 'Orbitron';
        top:-25px;
        width: 70%;
      }

      .innerWrap {

        >.inner {

          .searchBox{
            top: 25px;
            right: 10px;
          }

          >section {
      
            article {width: 33.33333%;}
          }
        } 
      } 
    }
  }
}

@media screen and (max-width: 679px){
  .gallery {

    figure{
      filter: saturate(20%);

      .galleryMian{
  
        .galleryContent{

          h1{font-size: 45px;}
        }
      }

      .innerWrap {

        >.inner {

          .searchBox{
            top: 25px;
            right: 10px;

            input[type=text]{width: 155px;}
          }

          .loadingAni{
            top: 150px;
        
            img{
        
              &:nth-child(1){animation: galleryAni04 10s;}
              
              &:nth-child(2){animation: galleryAni05 9s 1s;}
        
              &:nth-child(3){ animation: galleryAni06 7s 3s}
            }
          }

          >section {
      
            article {width: 50%;}
          }
        } 
      } 
    }
  }
}

@media screen and (max-width: $mobile){ 
  .gallery {

    figure{
      filter: saturate(50%);

      .galleryMian{
        padding:5px;

        .galleryContent{
          padding:30px 15px;
          width: 60%;

          h1{font-size: 30px;}
        }
      }

    
      h3{
        top:-40px;
        font:bold 38px/60px 'Orbitron';
      }

      .innerWrap {

        >.inner {

          .searchBox{
            top: 35px;
            right: 10px;

            input[type=text]{width: 150px;}
          }

          >section {
      
            article {
              width: 100%;
              padding:15px 30px;

              .inner{
                padding:20px;
              }
            }
          }
        } 
      } 
    }
  }
}